function adminUpdateModuleStatus(moduleID, status) {
    const isConfirmed = confirm(' ,    ?');

    if (!isConfirmed) {
        return; //   ,  
    }

    if(status === 'ACTIVE') {
        status = 'INACTIVE'
    }else{
        status = 'ACTIVE'
    }

    //    
    fetch(`/admin/modules/updateStatus/${moduleID}/${status}`, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
            'X-CSRF-TOKEN': document.querySelector('meta[name="_csrf"]')?.content || '' //  Spring Security CSRF
        }
    })
        .then(response => {
            return response.json().then(data => {
                if (!response.ok) {
                    throw new Error(data.message || '   ');
                }
                return data;
            });
        })
        .then(data => {
            //  
            showAlert(data.message, 'success');

            //      
            if (typeof loadModules === 'function') {
                void loadModules(window.currentModulesPage || 1);
            }
        })
        .catch(error => {
            //  
            showAlert(`: ${error.message}`, 'error');
            console.error('   :', error);
        });
}

window.adminUpdateModuleStatus = adminUpdateModuleStatus;
